import { EngineInitializer } from '../../../WebGL2Engine/src/EngineInitializer';
import { CameraComponent } from '../../../WebGL2Engine/src/scene/components/CameraComponent';
import { Entity } from '../../../WebGL2Engine/src/scene/Entity';
import { VolumeRenderComponent } from '../../../WebGL2Engine/src/scene/components/VolumeRenderComponent';
import { ParticleRenderComponent } from '../../../WebGL2Engine/src/scene/components/ParticleRenderComponent';
import { Scene } from '../../../WebGL2Engine/src/scene/Scene';
import { GLHelper } from '../../../WebGL2Engine/src/utils/GLHelper';
import { ResourceFactory } from '../../../WebGL2Engine/src/utils/ResourceFactory';
import vec3 from '../../../WebGL2Engine/src/utils/tsm/vec3';
import vec4 from '../../../WebGL2Engine/src/utils/tsm/vec4';
import { LightComponent } from '../../../WebGL2Engine/src/scene/components/LightComponent';

import $ from "jquery";

export default class ViewerScene extends Scene {
    mVolumeEntity : Entity;
    mRenderComponent : VolumeRenderComponent | ParticleRenderComponent;
    mRepresentation : string;
    mMaterial : string;
    mScale : number;

    mAzimut = 0;
    mInclination = 90;
    mCamDistance = 2.0;
    mouseStateDown : boolean = false;
  
    oldX : number;
    oldY : number;

    constructor(canvasID : string, sky? : number[][]) {
        super();
        EngineInitializer.init(canvasID);

        this.mMaterial = "SOLID";
        this.mRepresentation = "DENSITY";
        this.mScale = 0.1;

        const camera = this.createEntity();

        let cubeTex : WebGLTexture;

        if (sky) {
            cubeTex = ResourceFactory.createCubeTextureFromArray(sky);
        } else {
            cubeTex = ResourceFactory.createCubeTextureFromConstCol(new vec4([0.7,0.7,0.7,1]));
        }        

        camera.setCameraComponent(
            new CameraComponent(
                cubeTex,            
                60,   // fovy
                0.1,  // near
                10    // far
            )
        );
        this.updateCam();
        const light = this.createEntity();

        light.setLightComponent(new LightComponent(new vec3([1, 1, 1])));
        light.getTransform().setPosition(new vec3([1, 2, -2]));
        light.getTransform().setScale(new vec3([0.1, 0.1, 0.1]));
    
        this.mVolumeEntity = this.createEntity();
        this.mVolumeEntity.getTransform().setPosition(new vec3([0, 0.25, 0]));

        this.init();

        let th = this;       

        let canvas = $('#' + canvasID);
        canvas.mousedown(function(e : any) {
          th.mouseStateDown = true;
          th.oldX = null;
        });
        document.onmouseup = function(e : any) {
          th.mouseStateDown = false;
        };
        canvas.mousemove(function(e : any) {
          if(th.mouseStateDown) {
            th.duringDrag(e.pageX, e.pageY);
            th.update(0.1);
          }
        });
        canvas.on("wheel", function(e : any) {
          e.preventDefault();
          th.mCamDistance += 0.03 * (<WheelEvent>e.originalEvent).deltaY;
          th.mCamDistance = Math.min(Math.max(th.mCamDistance, 1.2), 4.0);
          th.update(0.1);
        });

        /*async function main() {     
            var lastT = performance.now();
            function loop() {
                let dt = (performance.now() - lastT) / 1000;
                lastT = performance.now();
                th.update(dt);
                th.render();
                GLHelper.finish();
                window.requestAnimationFrame(loop);
            } 
            loop();
        }
            
        main();*/
    }
    init() : void { }

    duringDrag(x : number, y : number) {
        if (!this.oldX) {
        this.oldX = x;
        this.oldY = y;
        }
        this.mAzimut -= x - this.oldX;
        this.mInclination -= y - this.oldY;

        if(this.mAzimut < -180) {
        this.mAzimut += 360;
        } else if(this.mAzimut > 180) {
        this.mAzimut -= 360;
        }
        if(this.mInclination < 10) {
        this.mInclination = 10;
        } else if(this.mInclination > 90) {
        this.mInclination = 90;
        }

        this.oldX = x;
        this.oldY = y;
    }
    
    updateCam(){
        let az_rad = this.mAzimut/180 * Math.PI;
        let inc_rad = this.mInclination/180 * Math.PI;
        let pos = new vec3([
          this.mCamDistance * Math.sin(inc_rad) * Math.sin(az_rad),
          this.mCamDistance * Math.cos(inc_rad),
          this.mCamDistance * Math.sin(inc_rad) * Math.cos(az_rad)
        ]);
        this.mCamera.getEntity().getTransform().setPosition(pos);
        this.mCamera.getEntity().getTransform().lookAt(new vec3([0, 0, 0]));
    }

    createRenderComponent(dim? : number[]) {
        switch(this.mRepresentation) {
            case "DENSITY":
            case "SDF":
                this.mRenderComponent = new VolumeRenderComponent(ResourceFactory.createFloatGrid(new vec3([dim[0], dim[1], dim[2]]), 1, new vec3([1,1,1])));
                break
            case "PARTICLE":
                this.mRenderComponent = new ParticleRenderComponent(ResourceFactory.createParticleSystemFromArray(new Float32Array([0,0,0]), this.mScale));
                break;
            default:
                console.error("Representation type not recognized! Expected one of these: DENSITY, SDF, PARTICLE!")
                break;
        }
        this.setMaterial();
    
        this.mVolumeEntity.setRenderComponent(this.mRenderComponent);
    }

    setMaterial() {
        switch(this.mMaterial) {
            case "SMOKE":
            case "DARK_SMOKE":
                this.mRenderComponent.setMaterialEntry("diffCol", [0.0,0.0,0.0,1.0]);
                this.mRenderComponent.setMaterialEntry("reflCol", [1, 1, 1, 0.0]);
                this.mRenderComponent.setMaterialEntry("specCol", [1, 1, 1, 0.0]);
                break;
            case "LIGHT_SMOKE":
                this.mRenderComponent.setMaterialEntry("diffCol", [1.0,1.0,1.0,0.5]);
                this.mRenderComponent.setMaterialEntry("reflCol", [1, 1, 1, 0.0]);
                this.mRenderComponent.setMaterialEntry("specCol", [1, 1, 1, 0.0]);
                break;
            case "LIQUID":
                this.mRenderComponent.setMaterialEntry("diffCol", [0.0,0.0,0.0,0.0]);
                this.mRenderComponent.setMaterialEntry("reflCol", [1, 1, 1, 1.0]);
                this.mRenderComponent.setMaterialEntry("specCol", [1, 1, 1, 1.0]);
                break;
            case "SOLID":
                this.mRenderComponent.setMaterialEntry("diffCol", [0.1,0.47,0.8,1.0]);
                this.mRenderComponent.setMaterialEntry("reflCol", [1, 1, 1, 0.0]);
                this.mRenderComponent.setMaterialEntry("specCol", [1, 1, 1, 0.0]);
                break;
            default:
                console.error("Material type not recognized! Expected one of these: SMOKE, DARK_SMOKE, LIGHT_SMOKE, LIQUID, SOLID!")
                break;
        }
    }
    
    setData(value : number[], dim? : number[], mat? : string, representation_type? : string, scale? : number) : void {
        var dirty = false;

        if(representation_type != undefined && representation_type != this.mRepresentation) {
            this.mRepresentation = representation_type;
            dirty = true;
        }

        if(this.mRenderComponent == undefined || dirty) {
            this.createRenderComponent(dim);
        } else if (dim != undefined && this.mRepresentation != "PARTICLE") {
            let v = (<VolumeRenderComponent>this.mRenderComponent).getVolumeTexture().getSize();
            if(dim[0] != v.x || dim[1] != v.y || dim[2] != v.z) {
                this.createRenderComponent(dim);
            }
        }

        if(scale != undefined && scale != this.mScale && this.mRepresentation == "PARTICLE") {
            this.mScale = scale;
            (<ParticleRenderComponent>this.mRenderComponent).getParticleSystem().setScale(this.mScale);
        }

        if(mat != undefined && mat != this.mMaterial) {
            this.mMaterial = mat;
            this.setMaterial();
        }

        switch(this.mRepresentation) {
            case "DENSITY":
                (<VolumeRenderComponent>this.mRenderComponent).getVolumeTexture().load(new Float32Array(value));
                (<VolumeRenderComponent>this.mRenderComponent).getVolumeTexture().multConst(-1.0);
                (<VolumeRenderComponent>this.mRenderComponent).getVolumeTexture().addConst(0.1);
                break;
            case "SDF":            
                (<VolumeRenderComponent>this.mRenderComponent).getVolumeTexture().load(new Float32Array(value));
                break;
            case "PARTICLE":
                (<ParticleRenderComponent>this.mRenderComponent).getParticleSystem().load(new Float32Array(value));
                break
        }
        this.update(0.1);
    }
    update(dt: number) : void {
        this.updateCam();
        this.render();
        GLHelper.finish();
    }
}
