import React, {Component} from 'react';
import PropTypes from 'prop-types';
import ViewerScene from "./WebglScene";

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class Webglviewer extends Component {
    scene = null;
    lastV = null;
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        this.scene = new ViewerScene(this.props.id, this.props.sky);
    }
    componentDidUpdate() {
        if (this.props.data != undefined && (
            this.lastV.data != this.props.data || 
            this.lastV.material_type != this.props.material_type ||
            this.lastV.representation_type != this.props.representation_type ||
            this.lastV.scale != this.props.scale)) {
            var dim = undefined;
            if(this.props.representation_type != "PARTICLE") {
                dim = [this.props.data.length, this.props.data[0].length, this.props.data[0][0].length];
            }
            this.scene.setData(this.props.data.flat(3), dim, this.props.material_type, this.props.representation_type, this.props.scale);
        } 
        this.lastV = this.props;
    }
    render() {
        return (
            <canvas id={this.props.id} width={800} height={600} style={{"width":"100%", "height":"100%"}}/>
        );
    }
}

Webglviewer.defaultProps = {};

Webglviewer.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string.isRequired,

    /**
     * A grid that will be rendered when this component is rendered.
     */
    data: PropTypes.array, 

    /**
     * Sky map, expects an array of six arrays.
     * Value have to be between 0 and 255.
     * Each array represents one side of the cube map (flattened pixels of an image).
     * Expects 4 channels (r,g,b,a).
     * Image has to be quadratic.
     */
    sky: PropTypes.array,

    /**
     * Material type used for rendering.
     * Possible types: SMOKE, DARK_SMOKE, LIGHT_SMOKE, SOLID, LIQUID
     */
    material_type: PropTypes.string,

    /**
     * Specifies the representation type used.
     * Possible types: DENSITY, SDF, PARTICLE
     */
    representation_type: PropTypes.string,

    /**
     * Particle scale.
     */
    scale: PropTypes.number,
};
